.square{
    background-color: red;
    width: 120px;
    height: 120px;
    border: 1px solid black;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.plaid {
    display: flex;
    flex-direction: row;
    border: 1px solid black;
}
  

.gender{
    margin: 0.5px;
    background-color: white;
    width: 70px;
    border-radius: 7px;
    font-weight: 500;
    text-align: center;
    font-size: 0.75em;
}

.number{
    display: inline-block;
    width: 122px;
    font-weight: 500;
    text-align: center;
}

